.role-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
}
.role-header__left {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}
.role-header__title {
  display: flex;
  align-items: center;
}

.role-name-input {
  font-size: 1.25rem;
  font-weight: 600;
  text-transform: capitalize;
  border: 1px solid transparent;
  background: transparent;
  padding: 0.4rem 0.6rem;
  border-radius: 8px;
  transition: all 0.15s ease;
}
.role-name-input:hover {
  border-color: var(--p-surface-200);
}
.role-name-input:focus {
  border-color: var(--p-primary-500);
  background: var(--p-surface-0);
  box-shadow: 0 0 0 2px var(--p-primary-100);
}

.permission-title {
  margin-bottom: 1.5rem;
  font-size: 1.1rem;
  font-weight: 600;
  color: var(--p-surface-800);
}

.permission-matrix {
  border: 1px solid var(--p-surface-100);
  border-radius: 12px;
  overflow: hidden;
}
.permission-matrix__header {
  display: flex;
  align-items: center;
  padding: 1rem 1.25rem;
  background: var(--p-surface-50);
  border-bottom: 1px solid var(--p-surface-100);
}
.permission-matrix__row {
  display: flex;
  align-items: center;
  padding: 0.85rem 1.25rem;
  border-bottom: 1px solid var(--p-surface-50);
  transition: background 0.12s ease;
}
.permission-matrix__row:last-child {
  border-bottom: none;
}
.permission-matrix__row:hover {
  background: var(--p-primary-50);
}
.permission-matrix__row--alt {
  background: var(--p-surface-0);
}
.permission-matrix__module-col {
  flex: 1;
  display: flex;
  align-items: center;
  gap: 0.75rem;
  min-width: 160px;
}
.permission-matrix__module-name {
  font-weight: 500;
  font-size: 0.9rem;
  color: var(--p-surface-700);
  text-transform: capitalize;
}
.permission-matrix__action-col {
  width: 120px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.35rem;
  flex-shrink: 0;
}
.permission-matrix__action-label {
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.04em;
  color: var(--p-surface-400);
}

:host-context(.dark) .role-name-input {
  color: var(--p-surface-100);
}
:host-context(.dark) .role-name-input:hover {
  border-color: var(--p-surface-600);
}
:host-context(.dark) .role-name-input:focus {
  background: var(--p-surface-800);
  box-shadow: 0 0 0 2px var(--p-primary-800);
}
:host-context(.dark) .permission-title {
  color: var(--p-surface-100);
}
:host-context(.dark) .permission-matrix {
  border-color: var(--p-surface-700);
}
:host-context(.dark) .permission-matrix__header {
  background: var(--p-surface-800);
  border-bottom-color: var(--p-surface-700);
}
:host-context(.dark) .permission-matrix__row {
  border-bottom-color: var(--p-surface-800);
}
:host-context(.dark) .permission-matrix__row:hover {
  background: var(--p-surface-800);
}
:host-context(.dark) .permission-matrix__module-name {
  color: var(--p-surface-200);
}
